
@font-face {
  font-family: 'MyFont';
  src: url('./assets/fonts/Alexandria-VariableFont_wght.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
:root {
  --main-color: #90b524;
  --background-color: white;
  --text-color: #242424;
  --title-font-lagre:48px;
  --title-font:40px;
  --title-font-one:32px;
  --title-font-two:24px;
  --title-font-three:20px;
  --title-font-normal:16px;
  --title-font-descrption:18px;
  --title-font-descrption-two:14px;
  --title-font-descrption-three:12px;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'MyFont', sans-serif;
  line-height: 2;
  -webkit-tap-highlight-color: transparent;
}

body {
  direction: rtl;
  color: var(--text-color);
  scroll-behavior: smooth;
}

a{
  text-decoration: none;
}
button{
  transition: 0.2;
  cursor: pointer;
}
button:hover{
  opacity: 0.9;
}
@media screen and (max-width:600px) {
  :root {

    --title-font-lagre:44px;
    --title-font:36px;
    --title-font-one:28px;
    --title-font-two:20px;
    --title-font-three:16px;
    --title-font-normal:14px;
    --title-font-descrption:16px;
    --title-font-descrption-two:12px;
    --title-font-descrption-three:10px;
  }
}

.section-header {
  margin-inline: auto;
  max-width: 550px;
  text-align: center;
}
.section-header .title {
  font-size: var(--title-font-two);
  font-weight: 500;
  margin-bottom: 12px;
}
.section-header .title span {
  color: var(--main-color);
}
.section-header .descrption {
  padding-block: 12px;
  font-size: var(--title-font-descrption);
  color: #383838;
}
.More {
  margin-top: 48px;
  display: inline-block;
  padding: 12px 36px;
  border-radius: 12px;
  font-size: var(--title-font-normal);
  background-color: var(--main-color);
  color: var(--background-color);
  transition: 0.2s;
}
.More:hover {
  opacity: 0.9;
}
.hidden {
  display: none;
}
